.grid {
  display: grid;
  max-width: 100%;
   grid-template-columns: 300px minmax(100px, auto);
   /* grid-template-columns: 300px auto; */
 grid-template-rows: 1fr minmax(700px, auto) 1fr;
  grid-gap: 10px;

  grid-template-areas:
    "second first"
    "second third"
    "second third"
    "fourth third";

    /* grid-template-areas:
      "second first"
      "third third"
      "third third"
      "fourth third"; */
}


.item {

  /* display: grid; */
  grid-auto-flow: column;
  background-color: #111;
  color: #ace;
  /* display: flex; */
  /* flex-direction: column; */
