      align-items: center;

  display: -ms-flexbox;

  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
}


.item-1 {
  display: -ms-flexbox;
  display: flex;

  /* background-color: #ace; */
  grid-area: first;

}

.item-2 {
  display: -ms-flexbox;
  display: flex;
