/*-----------------------------------------------------------*/
/* ---------------------RESPONSIVE-------------------------- */

@media screen and (max-width: 350px) {

  h1 {
    color: #e93;
    font-size: 0.8em;
    letter-spacing: 2px;
  }

  .pb {
    display: none;
  }

}

/*-----------------------------------------------------------*/

@media screen and (min-width: 351px) and (max-width: 600px) {

  h1 {
    color: #e93;
    font-size: 1em;
    letter-spacing: 2px;
  }

  .pb {
    display: none;
  }

  .grid {
    display: grid;
    max-width: 100%;
     grid-template-columns: minmax(100px, auto);

    grid-template-rows: 70px 1fr minmax(700px, auto) 40px;
    grid-gap: 5px;


    /* display: flex;
    flex-flow: column;
    height: 100%; */
    grid-template-areas:
      "first"
      "second"
      "third"
      "fourth";
  }

}

/*-----------------------------------------------------------*/

@media screen and (min-width: 601px) and (max-width: 800px) {

  h1 {
    color: #e93;
    font-size: 2em;
    letter-spacing: 2px;

  }

  .pb {
    display: none;
  }


  .grid {
    height: 100%;
    display: grid;


    max-width: 100%;
     grid-template-columns: minmax(100px, auto);
     /* grid-template-columns: 300px auto; */
    grid-template-rows: 70px 1fr minmax(700px, auto) 40px;
    grid-gap: 5px;


    /* display: flex;
    flex-flow: column;
    height: 100%; */
    grid-template-areas:
      "first"
      "second"
      "third"
      "fourth";
  }

}

/* --------------------END RESPONSIVE----------------------- */
